@import "common.scss";
@import "icon.scss";
@import "button.scss";


.demo-transition .transition-box {
  margin-bottom: 10px;
  width: 200px;
  height: 100px;
  border-radius: 4px;
  background-color: #409eff;
  text-align: center;
  color: #fff;
  padding: 40px 20px;
  margin-right: 20px;
  box-sizing: border-box;
  font-size: 16px;
}

.demo-block.demo-progress .bin-progress {
  margin-bottom: 15px;
  width: 350px;
}
.demo-block .demo-loading {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin-right: 5px;
  width: 200px;
  height: 100px;
  border-radius: 4px;
  border: 1px solid #cecece;
}
.demo-badge.demo-block .item {
  margin-right: 40px;
}

.demo-input.demo-block .input-item {
  width: 300px;
  margin-right: 20px;
}
.demo-rate .block {
  padding: 30px 0;
  text-align: center;
  border-right: 1px solid #eff2f6;
  display: inline-block;
  width: 49%;
  box-sizing: border-box;
}
.demo-rate .block:last-child {
  border-right: none;
}

.demo-rate .demonstration {
  display: block;
  color: #8492a6;
  font-size: 14px;
  margin-bottom: 20px;
}

.doc-update .bin-timeline .bin-timeline-item-content {
  top: -8px;
}
.doc-update {
  .version {
    display: inline-block;
    padding: 0 10px;
    font-size: 18px;
    font-weight: 500;
    border: 1px solid #1890ff;
    border-radius: 5px;
    border-top: none;
    border-bottom: none;
    margin: 0;
  }
  .time {
    display: inline-block;
    font-size: 14px;
    padding-left: 20px;
    margin: 0;
  }
  .content {
    position: relative;
    padding-left: 15px;
    line-height: 1.5715;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 8px;
      width: 0;
      height: 0;
      transform: rotateZ(90deg);
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-bottom: 6px solid #5e6d82;
    }
  }
}

.demo-carousel-panel {
  height: 200px;
  line-height: 200px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  background: #99a9bf;
}

.global-anchor {
  position: fixed;
  top: 100px;
  right: 0;
  max-height: calc(100vh - 120px);
  padding: 5px;
  width: 158px;
  background: #ffffff;
  z-index: 5;
  overflow-y: auto;
}
